// RootLayout.jsx
import React, { useState, useEffect } from "react";
import MainNavigation from "./MainNavigation";
import { Outlet } from "react-router-dom";
import Drawer from "./Drawer";
import styled from "styled-components";
import { motion } from "framer-motion";
import useIsMobile from "./useIsMobile.js";

// Define breakpoints for responsiveness
const breakpoints = {
  mobile: "480px",
  tablet: "768px",
  desktop: "1024px",
};

// Container  fullHeight prop DOM    
const Container = styled(motion.div).withConfig({
  shouldForwardProp: (prop) => !["fullHeight"].includes(prop),
})`
  display: flex;
  flex-direction: column;
  border: 2px solid #f5f5f5;
  margin-top: 100px !important;
  transform-origin: center center;
  height: ${(props) => (props.fullHeight ? "100vh" : "auto")}; /*   */
  padding: 0;
  width: 92%; /*    */
  max-width: 1200px; /*    */
  margin: 0 auto; /*   */

  @media (max-width: ${breakpoints.desktop}) {
    height: ${(props) => (props.fullHeight ? "100vh" : "auto")};
    padding: 0 0.75rem;
    margin-top: 20px !important;
    border: 2px solid #f5f5f5; /*   border   */
    width: 95%; /*     */
  }

  @media (max-width: ${breakpoints.tablet}) {
    height: ${(props) => (props.fullHeight ? "100vh" : "auto")};
    padding: 0 0.75rem;
    margin-top: 10px !important;
    border: 2px solid #f5f5f5; /*   border   */
    width: 95%; /*     */
  }

  @media (max-width: ${breakpoints.mobile}) {
    padding: 0 0.5rem;
    border: none; /*  border  */
    width: 92%; /*     */
    margin-top: 3px !important;
  }
`;

const MainContent = styled.main`
  flex: 1;
  padding: 2rem;

  @media (max-width: ${breakpoints.tablet}) {
    padding: 1.5rem;
  }

  @media (max-width: ${breakpoints.mobile}) {
    padding: 1rem;
  }
`;

const RootLayout = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [fullHeight, setFullHeight] = useState(false);
  const isMobile = useIsMobile(); // Use the custom hook

  //    
  const [language, setLanguage] = useState("");

  useEffect(() => {
    if (drawerOpen) {
      setFullHeight(true); // Apply fullHeight when drawer is open
    } else {
      const timer = setTimeout(() => {
        setFullHeight(false); // Remove fullHeight 1 second after drawer is closed
      }, 500);

      return () => clearTimeout(timer); // Clear timer on cleanup
    }
  }, [drawerOpen]);

  const toggleDrawerHandler = () => {
    setDrawerOpen((prevState) => !prevState);
  };

  // Define animation variants
  const variants = {
    open: {
      scale: 0.85,
      translateY: "-5vh",
      transition: {
        type: "spring",
        stiffness: 180,
        damping: 25,
        mass: 1,
        restDelta: 0.001,
      },
    },
    closed: {
      scale: 1,
      translateY: "0",
      transition: {
        type: "spring",
        stiffness: 180,
        damping: 35,
        mass: 1,
        restDelta: 0.001,
      },
    },
  };

  return (
    <>
      <Container
        fullHeight={fullHeight}
        initial="closed"
        animate={isMobile ? "closed" : drawerOpen ? "open" : "closed"}
        variants={variants}
      >
        <MainNavigation
          drawerOpen={drawerOpen}
          onToggleDrawer={toggleDrawerHandler}
          language={language}
          setLanguage={setLanguage}
        />
        <MainContent>
          <Outlet context={{ language }} />
        </MainContent>
      </Container>
      <Drawer open={drawerOpen} onClose={toggleDrawerHandler} />
    </>
  );
};

export default RootLayout;
