// src/components/Drawer.js
import React, { useState, useRef, useEffect } from "react";
import styled from "styled-components";
import styles from "./Drawer.module.scss";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHome,
  faUser,
  faPhone,
  faEnvelope,
} from "@fortawesome/free-solid-svg-icons";
import { motion } from "framer-motion";
import { PulseLoader } from "react-spinners";

import spinnerStyles from "../../layout/user/Spinner.module.scss";

// Define breakpoints for responsiveness
const breakpoints = {
  mobile: "480px",
  tablet: "768px",
  desktop: "1024px",
};

const DrawerContainer = styled(motion.div)`
  font-family: "NotoSansKR";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: auto;
  width: 108px;
  height: 100%;
  background-color: none;
  z-index: 1400;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center; 

  @media (max-width: ${breakpoints.desktop}) {
    margin-top: 100px;
    justify-content: flex-start; 
  }
  @media (max-width: ${breakpoints.tablet}) {
    margin-top: 100px;
    justify-content: flex-start; 
    width: 138px;
  }

  @media (max-width: ${breakpoints.mobile}) {
    width: 20%;
    height: 100%;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.0);
  }
`;

// NavItem  
const NavItem = styled(motion.a)`
  margin: 25px 0;
  color: #333;
  font-size: 55px;
  cursor: pointer;
  text-decoration: none; /*    */

  &:hover {
    color: rgb(255, 157, 0);
  }

  @media (max-width: ${breakpoints.tablet}) {
    width: 80px;
    // padding: 1.5rem 0.75rem;
    margin: 5px 0;
  }

  @media (max-width: ${breakpoints.mobile}) {
    font-size: 35px;
    margin-left: 40px;

    a {
      font-size: 35px;
    }
  }
`;

//    
const iconVariants = {
  hidden: { 
    opacity: 0, 
    y: 0 //  
  },
  visible: (i) => ({
    opacity: 1,
    y: [20, -15, 10, 0], //  
    transition: {
      delay: i * 0.2, //     
      duration: 1.5, //    
      ease: [0.22, 1, 0.36, 1], //  easing
      type: "spring",
      stiffness: 600, //  
      damping: 15, //  
    },
  }),
};

//         
const slideHover = {
  x: -20, //  20px 
  transition: {
    type: 'spring',
    stiffness: 300,
    damping: 20,
  },
};



const TopDrawerContainer = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  background-color: #333;
  z-index: 2000;

  @media (max-width: ${breakpoints.desktop}) {
    display: none;
  }

  @media (max-width: ${breakpoints.tablet}) {
    display: none;
  }

  @media (max-width: ${breakpoints.mobile}) {
    display: none;
  }
`;

const BottomDrawerContainer = styled(motion.div)`
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 120px;
  background-color: #none;
  z-index: 1200;

  @media (max-width: ${breakpoints.tablet}) {
    display: none;
  }

  @media (max-width: ${breakpoints.mobile}) {
    display: none;
  }
`;

const LeftDrawerContainer = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 66px;
  background-color: #none;
  z-index: 1500; /* Increased z-index */

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;

  @media (max-width: ${breakpoints.tablet}) {
    width: 0px;
    padding: 1.5rem 0.75rem;
  }

  @media (max-width: ${breakpoints.mobile}) {
    display: none;
  }
`;

const CloseButton = styled(motion.button)`
  position: absolute;
  top: 20%;
  left: -5%;
  width: 90px;
  height: 90px;
  background-color: #333;
  border: 5px solid #333;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 1700;
  opacity: ${(props) => (props.open ? 1 : 0)};
  visibility: ${(props) => (props.open ? "visible" : "hidden")};
  transition: opacity ${(props) => (props.open ? "1.0s ease-in-out" : "0s")},
    visibility 0s ${(props) => (props.open ? "0s" : "1.0s")};
  animation: ${(props) => (props.open ? "rotateIn 0.8s ease-in-out" : "none")};

  box-sizing: border-box; /* :       */

  .x-shape {
    width: 60%;
    height: 60%;
    position: relative;
    z-index: 1600;
  }

  .x-shape::before,
  .x-shape::after {
    content: "";
    position: absolute;
    width: 0;
    height: 5px;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform-origin: center;
    transition: width 0.3s ease-in-out;
  }

  .x-shape::before {
    transform: translate(-50%, -50%) rotate(45deg);
    animation: drawXBefore 0.3s ease-in-out forwards;
    animation-delay: 0.3s;
    z-index: 1700;
  }

  .x-shape::after {
    transform: translate(-50%, -50%) rotate(-45deg);
    animation: drawXAfter 0.3s ease-in-out 0.15s forwards;
    animation-delay: 0.3s;
    z-index: 1700;
  }

  @keyframes drawXBefore {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }

  @keyframes drawXAfter {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }

  @keyframes rotateIn {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @media (max-width: ${breakpoints.tablet}) {
    width: 50px;
    height: 50px;
    left: -10px;
  }
`;

const Drawer = ({ open, onClose }) => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate(); //    useNavigate
  const drawerRef = useRef(null);
  const drawerRefRight = useRef(null);
  const drawerRefLeft = useRef(null);

  const handleNavClick = async (path) => {
    setLoading(true); //  
    try {
      //      
      await new Promise((resolve) => setTimeout(resolve, 1000)); //     
      navigate(path); //  
      setTimeout(() => {
        onClose(); //    Drawer 
      }, 300); // navigate     onClose 
    } finally {
      setLoading(false); //  
    }
  };
  //   useRef, effect  ?

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (
        drawerRefRight.current &&
        !drawerRefRight.current.contains(e.target) &&
        drawerRefLeft.current &&
        !drawerRefLeft.current.contains(e.target)
      ) {
        onClose();
      }
    };

    if (open) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [open, onClose]);

  return (
    <>
      {loading && (
        <div className={styles.loadingOverlay}>
          <div className={spinnerStyles.spinnerContainer}>
            <PulseLoader
              className={spinnerStyles.loader}
              color="#0B593F"
              loading={loading}
              size={18}
            />{" "}
          </div>
        </div>
      )}
      <DrawerContainer
        initial={{ x: "100%" }} // Drawer   
        animate={{ x: open ? 0 : "100%" }} //     
        transition={{
          type: "spring", // inertia  spring 
          stiffness: 180, //  
          damping: open ? 25 : 35, //  
          mass: 1, // 
          restDelta: 0.001, //   
        }}
        open={open}
        ref={drawerRef}
      >
        {[
          { icon: faHome, path: "/", label: "Home" },
          { icon: faUser, path: "/about", label: "About Me" },
          { icon: faPhone, path: "/contact", label: "Contact" },
          { icon: faEnvelope, href: "mailto:limjhoon8@gmail.com", label: "Email" },
        ].map((item, index) => (
          <NavItem
            key={index}
            custom={index}
            initial="hidden"
            animate={open ? "visible" : "hidden"}
            variants={iconVariants}
            onClick={() => item.path ? handleNavClick(item.path) : null}
            aria-label={item.label}
            href={item.href || "#"}
            target={item.href ? "_blank" : "_self"}
            rel={item.href ? "noopener noreferrer" : ""}
            whileHover={slideHover}
          >
            <FontAwesomeIcon icon={item.icon} />
          </NavItem>
        ))}
        {/* <NavItem>
          <a
            href="https://www.instagram.com/jehoon2001"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={faInstagram} />
          </a>
        </NavItem> */}
      </DrawerContainer>
      <TopDrawerContainer
        initial={{ y: "-100%" }} //  
        animate={{ y: open ? 0 : "-100%" }} //     
        transition={{
          type: "spring", // inertia  spring 
          stiffness: 180, //  
          damping: open ? 27 : 33, //  
          mass: 1, // 
          restDelta: 0.001, //   
        }}
        open={open}
      ></TopDrawerContainer>
      <BottomDrawerContainer
        initial={{ y: "100%" }} //  
        animate={{ y: open ? 0 : "100%" }} //     
        transition={{
          type: "spring", // inertia  spring 
          stiffness: 180, //  
          damping: open ? 27 : 33, //  
          mass: 1, // 
          restDelta: 0.001, //   
        }}
        open={open}
      ></BottomDrawerContainer>
      <LeftDrawerContainer
        initial={{ x: "-100%" }}
        animate={{ x: open ? 0 : "-100%" }}
        transition={{
          type: "spring",
          stiffness: 180,
          damping: open ? 25 : 35,
          mass: 1,
          restDelta: 0.001,
        }}
        open={open}
        ref={drawerRef}
      >
        <CloseButton
          open={open}
          onClick={onClose}
          whileHover={{ scale: 1.2 }}
          whileTap={{ scale: 0.9 }}
          transition={{ type: "spring", stiffness: 400, damping: 17 }}
        >
          <div className="x-shape"></div>
        </CloseButton>
      </LeftDrawerContainer>
    </>
  );
};

export default Drawer;
